@mixin rwd($w) {
  @media only screen and (max-width: $w) {
    @content; } }
@mixin minW($w) {
  @media only screen and (min-width: $w) {
    @content; } }
@mixin rwdh($h:900px) {
  @media only screen and (max-height: $h) {
    @content; } }
@mixin minH($h:900px) {
  @media only screen and (min-height: $h) {
    @content; } }


@mixin clearfix {
  &:after {
    content: '';
    display: block;
    clear: both; } }

@mixin keyframes($name) {
	@-webkit-keyframes #{$name} {
		@content; }
	@-moz-keyframes #{$name} {
		@content; }
	@-ms-keyframes #{$name} {
		@content; }
	@keyframes #{$name} {
		@content; } }
@mixin animation($content) {
	-webkit-animation: $content;
	-moz-animation: $content;
	-o-animation: $content;
	animation: $content; }

@mixin before($txt:'') {
  &:before {
    content: $txt;
    display: block;
    @content; } }

@mixin after($txt:'') {
  &:after {
    content: $txt;
    display: block;
    @content; } }

@mixin hover($w:768px) {
  @media only screen and (min-width: $w) {
    &:hover {
      @content; } } }


@mixin pstc0($t:0,$l:0,$b:0,$r:0) {
  position: absolute;
  top: $t;
  bottom: $b;
  left: $l;
  right: $r;
  margin: auto; }

@mixin pstc5($t:50%,$l:50%) {
  position: absolute;
  top: $t;
  left: $l;
  transform: translate(-50%,-50%); }

@mixin fontH1() {
  font-size: 1.5rem;
  line-height: 1.5; }

@mixin fontH2() {
  font-size: 1.333333333333333rem;
  line-height: 1.5; }

@mixin fontH3() {
  font-size: 1.166666666666667rem;
  line-height: 1.5; }

@mixin fontP() {
  font-size: 1rem;
  line-height: 1.5; }

@mixin dib {
  display: inline-block;
  vertical-align: middle; }

$red: #cc0000;
$yellow: #ffff00;


* {
  padding: 0;
  margin: 0px;
  // outline: 1px solid #f00
  font-family: 'Roboto','Noto Sans TC', sans-serif; }

p,a {
  padding: 0;
  margin: 0;
  text-decoration: none; }

img,iframe {
  width: 100%;
  vertical-align: top; }

html {
  font-size: 20px; }

body {
  // background-color: #f3c100
  font-size: 0; }

hr {
  margin: 0.5rem 0; }

h1 {
  @include dib;
  @include fontH1; }
h2 {
  @include fontH2; }

#sidebar {
  width: 220px;
  height: 100vh;
  font-size: 1rem;
  background-color: #212529;
  overflow-y: auto;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  .sidebar-wrapper {
    width: 100%;
    height: 100%;
    padding: 15px;
    box-sizing: border-box;
    color: #fff;
    .sidebar-title {
      // padding-bottom: 20px
      font-size: 1.2rem; }
    .box {
      a.link {
        padding: 10px 5px;
        display: block;
        color: #fff;
        border-radius: .2rem;
        transition: .3s;
        p {
          font-size: 1rem; }
        &:hover,&.active {
          color: #333;
          background-color: #fff;
          text-decoration: none; } }
      .sub-list {
        a.link {
          padding: 5px 0px 5px 20px;
          p {
            font-size: 0.8rem; } } } } } }





.body-wrapper {
  width: calc(100% - 220px);
  min-height: 100vh;
  padding: 15px;
  padding-top: 70px;
  margin-left: 220px;
  font-size: 1rem;
  background-color: #ECF0F5;
  box-sizing: border-box;
  position: relative; }

header {
  width: 100%;
  color: #fff;
  background-color: #3C8DBB;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  .wrapper {
    margin: .5rem 1rem;
    text-align: right;
    .user-name,.logout {
      font-size: 1rem;
      margin: 0 .5rem;
      @include dib; }
    .logout {
      cursor: pointer; } } }


