form {
  .wrapper {
    margin: 20px 0;
    // display: flex
    // justify-content: space-between
    // flex-wrap: wrap
    // olumn-count: 3
    // column-gap: 0
    &.c-2 {
      column-count: 2;
      column-gap: 15px; }
    .content {
      break-inside: avoid;
      width: 100%;
      padding: 15px;
      margin-bottom: 15px;
      background-color: #fff;
      box-sizing: border-box;
      border-top: 2px solid #3C8DBB;
      h2 {
