      .form-label {
        margin-bottom: 0rem;
        font-size: 0.8rem;
        font-weight: bold;
        span.remark {
          padding: 0 0.5rem;
          font-size: .6rem; } }
      .file-img {
        width: 200px;
        max-height: 200px;
        margin: 10px 0;
        object-fit: contain; }
      .items-title {
        font-size: 0.8rem; }
      .items-box {
        width: 100%;
        padding: 10px;
        border: 1px solid rgba(#3C8DBB,.3);
        border-radius: 5px;
        box-sizing: border-box;
        .items {
          padding: 10px;
          border-radius: 5px;
          background-color: rgba(#3C8DBB,.1);
          & + .items {
            margin-top: 10px; } } }
      .btn-add {
        margin: 10px 0; } } }
  .page-data {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -100;
    opacity: 0; }
  .btn-box {
    text-align: center;
    button.btn {
      width: 100px;
      margin: 0 .5rem;
      display: inline-block; } } }

